@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.title {
    text-align: center;
    vertical-align: center;
    color : white;
    font-family: 'Montserrat', sans-serif;
    transform: scale(1.5);
    animation: appear 1s ease-in;
    padding-top: 25vh;
    width : 30vw;
    margin-left : 35vw;
    position: relative;
}
@keyframes appear {
    0% {
        opacity: 0;
        top : 35px;
    }
    100% {
        opacity: 1;
        top : 0;
    }
}
body {
    margin : 0;
    padding : 0;
}