.App {
  text-align: center;
}
body {
  margin : 0;
  background-image: url('https://2.bp.blogspot.com/-j415UAILa6k/TwKisnQ7HjI/AAAAAAAABbE/eFUfN23MF4o/s1600/england-background-14-786022.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow-x: hidden;
}
.navbar {
  position: fixed;
  margin: 0;
  padding: 0;
  z-index: 999;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
