@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.all {
    padding : 5vh;
    padding-left: 20vh;
    padding-right: 20vh;
    padding-top : 15vh;
    background-image: url('https://2.bp.blogspot.com/-j415UAILa6k/TwKisnQ7HjI/AAAAAAAABbE/eFUfN23MF4o/s1600/england-background-14-786022.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    overflow-x: hidden;
    .title {
        text-align: center;
        color : white;
        transform: scale(1.6);
        font-family : 'Montserrat',sans-serif;
        position: relative;
        animation: appear 1s ease-in;
        padding-top: 10vh;
    }
    .description {
        display: flex;
        margin-left : 10vh;
        padding-top: 10vh;
        position: relative;
        .text {
            border-right: solid black 1px;
            margin-left : 12vh;
            padding-right: 5vh;
            h1,p {
                font-family: 'Montserrat',sans-serif;
            }
            p {
                max-width : 25vw;
                color: white;
                text-align: center;
            }
            h1 {
                text-align: center;
                color: white;
            }
        }

        img {
            width : 30vw;
            height : 25vw;
            margin-left : 15vw;
        }
    }
}
@keyframes appear {
    0% {
        opacity: 0;
        top : 35px;
    }
    100% {
        opacity: 1;
        top : 0;
    }
}